<template>
  <div class="booking-main">
  	<div class="booking-header">
      <a @click="goBack()">
        <img class="icon-black" src="../../assets/images/My/sp_icon_back.png">
      </a>
      <p class="header-title">提交订单</p>
    </div>
    <div class="booking-center">
    	<div class="booking-order-info">
    		<div class="booking-spu-title">香港迪士尼乐园【电子票】</div>
	    	<div class="booking-sku-title">1日长者门票（65周岁或以上）</div>
	    	<div class="booking-num"> 数量：1</div>
    	</div>
    	<div class="booking-info-list">
    		<div class="booking-info-item">
    			<div class="booking-info-name">姓名</div>
    			<input class="booking-info-input" type="text" name="booking-info" placeholder="请输入姓名"/>
    		</div>
    		<div class="booking-info-item">
    			<div class="booking-info-name">电话</div>
    			<input class="booking-info-input" type="text" name="booking-info" placeholder="请输入电话"/>
    		</div>
    		<div class="booking-info-item">
    			<div class="booking-info-name">邮箱</div>
    			<input class="booking-info-input" type="text" name="booking-info" placeholder="请输入邮箱"/>
    		</div>
    		<div class="booking-info-item">
    			<div class="booking-info-name">地址</div>
    			<input class="booking-info-input" type="text" name="booking-info" placeholder="请输入地址"/>
    		</div>
    		<div class="booking-info-item">
    			<div class="booking-info-name">身份证</div>
    			<input class="booking-info-input" type="text" name="booking-info" placeholder="请输入身份证"/>
    		</div>
    	</div>

    </div>
    <!-- 底部按钮 -->
      <div class="booking-footer">
        <div class="booking-footer-num">
          <span class="booking-footer-cur">￥</span>
          <span class="booking-footer-price">177</span>
        </div>
        <router-link to="/payment" class="booking-footer-btn">
          立即预定
        </router-link>
      </div>
  </div>
</template>

<script>
export default {
  name: 'Booking',
  methods: {
     goBack: function () { 
      this.$router.go(-1);
    },
  }
}
</script>

<style>
.booking-title{
   font-weight: bold;
  height: 0.8rem;
  line-height: 0.9rem;
  padding-right: 0.3rem;
  border-bottom: 0.01rem  solid #f0f0f0;
  background-color: #fff;
  font-size: 0.28rem;
}

.booking-main{
  display: flex;
  flex-direction: column;
  padding-bottom: 0.9rem;
}
.booking-header{
  position: fixed;
  width: 100%;
  /* margin-top: 0.3rem; */
  background: #fff;
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  /* border-bottom: 1px solid #ddd;  */
}
.icon-black{
  position: absolute;
  top: 0.25rem;
  left: 0.35rem;
  float: left;
  height: 0.4rem;
  width: 0.2rem;
}
.booking-center{
	margin-top: 0.9rem;
}
.booking-order-info{
	padding-left: 0.3rem;
	background-color: #fff;
}
.booking-spu-title{
	line-height: 0.86rem;
 	font-size: 0.28rem;
    border-bottom: 0.01rem  solid #f0f0f0;
	color: #1a1a1a;
}
.booking-sku-title{
	line-height: 0.86rem;
	font-size: 0.26rem;
 	border-bottom: 0.01rem  solid #f0f0f0;
	color: #1a1a1a;
}
.booking-num{
	line-height: 0.86rem;
	font-size: 0.26rem;
	color: #1a1a1a;
}
.booking-info-input{
  background-color: #fff;
}
.booking-info-list{
	font-size: 0.26rem;
	padding-left: 0.3rem;
	margin-top: 0.1rem;
	line-height: 0.9rem;
	background-color: #fff;
}
.booking-info-item{
	height: 0.86rem;
	display: flex;
	flex-direction: row;
 	border-bottom: 0.01rem  solid #f0f0f0;
}
.booking-info-name{
	width: 24%;
}
/* 底部按钮 */
.booking-footer{
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 0.9rem;
  z-index: 101;
  background: #fff;
  display: flex;
  flex-direction: row;
  overflow: hidden;
  justify-content: space-between;
  box-shadow:0 -0.05rem 0.1rem #ebebeb;
}
.booking-footer-num{
  width: 70%;
  line-height: 0.9rem;
  color: #ff6631;
  margin-left: 0.3rem;
}
.booking-footer-cur{
  font-size: 0.28rem;
}
.booking-footer-price{
  
  font-size: 0.6rem;
}
.booking-footer-btn{
  width: 30%;
  text-align: center;
  line-height: 0.9rem;
  background-color: #17a1e6;
  color: #fff;
  font-size: 0.3rem;
}
</style>



