<template>
  <div class="detail-main">
    <div class="detail-header">
      <a @click="goBack()">
        <img class="icon-black" src="../../assets/images/My/sp_icon_back.png">
      </a>
      <p class="header-title">订单详情</p>
    </div>
    <div class="orderdetail-info">
      <div class="orderdetail-item">
        <div class="orderdetail-item-info">
          <div class="orderdetail-info-img">
            <img src="../../assets/images/My/order-pic.jpg">
            <div class="orderdetail-info-img-type">{{item.orderStatus}}</div>
          </div>
          <div class="orderdetail-info-text">
            <div class="orderdetail-info-spu">{{item.productTitle}}</div>
            <div class="orderdetail-info-sku">{{item.skuName}}</div>
            <div class="orderdetail-info-bot">
              <div class="orderdetail-info-num">数量： {{item.num}}张</div>
              <router-link to="/aftersale/1" class="orderdetail-item-btn">申请售后</router-link>
            </div>
          </div>
        </div>
      </div>
      <div class="orderdetail-text">
        <h2 class="orderdetail-title">预订信息</h2>
        <p class="orderdetail-orderNum">订单编号：{{item.orderSonNum}}</p>
        <p class="orderdetail-booking-date">下单时间：{{item.bookingDate}}</p>
      </div>
    </div>
    <a class="orderdetail-btn">查看电子凭证</a>
    <a class="orderdetail-btn">查看PDF凭证</a>
  </div>
</template>

<script>
export default {
  name: 'OrderDetail',
  data () {
    return {
      orderSonId: 1,
      item: {orderSonNum: "WX150069478702222052", orderSonId: "1156", orderStatus: "已发货", orderStatusId: 1,  productTitle: "香港优异明信片主题咖啡厅【电子票】", skuName: "套餐：咖啡买一送一优惠券", num: 1 ,bookingDate: "2018-10-1 11-41-31"},

    }
  },
  methods:{
    
    goBack: function () { 
      this.$router.go(-1);
    },
  },
  created: function(){
      this.orderSonId = this.$route.params.id;
  },
}
</script>

<style>
.detail-main{
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}
.detail-header{
  position: fixed;
  z-index: 100;
  width: 100%;
  /* margin-top: 0.3rem; */
  background: #fff;
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  /* border-bottom: 0.01rem solid #ddd;  */
}
.icon-black{
  position: absolute;
  top: 0.25rem;
  left: 0.35rem;
  float: left;
  height: 0.4rem;
  width: 0.2rem;
}
.header-title{
  font-size: 0.35rem;
}

.orderdetail-info{
  margin-top: 0.8rem;
}
.orderdetail-item{
  padding-left: 0.28rem;
  background-color: #fff;
  margin: 0.05rem 0;
}
.orderdetail-item-ordernum{
  padding: 0.2rem 0;
  font-size: 0.2rem;
  border-bottom: 0.01rem solid #ddd; 
}
.orderdetail-item-info{
  padding: 0.2rem 0.28rem 0.2rem 0;
  display: flex;
  flex-direction: row; 
}
.orderdetail-info-img{
  height: 1.2rem;
  width: 2rem;
  position: relative;
  border-radius: 0.05rem;
  overflow: hidden;
}
.orderdetail-info-img img{
  width: 100%;
  height: 100%;

}
.orderdetail-info-img-type{
  width: 0.66rem;
  height: 0.36rem;
  position: absolute;
  font-size: 0.18rem;
  line-height: 0.40rem;
  color: #fff;
  text-align: center;
  background-color: #3399cc;
  top: 0;
  left: 0;
}
.orderdetail-info-text{
  display: flex;
  flex-direction: column;
  margin-left: 0.28rem;
  justify-content: space-between;
  color: #1a1a1a;
}
.orderdetail-info-spu{
  font-size: .28rem;
  line-height: 0.35rem;
}
.orderdetail-info-sku{
  line-height: 0.35rem;
  font-size: .24rem;
}
.orderdetail-info-bot{
  display: flex;
  line-height: 0.33rem;
  flex-direction: row;
  justify-content: space-between;
}
.orderdetail-info-num{
  font-size: .24rem;
}
.orderdetail-item-btn{
  width: 1.16rem;
  height: 0.36rem;
  line-height: 0.4rem;
  border-radius: 0.2rem;
  text-align: center;
  font-size: 0.22rem;
  color: #aaa;
  background-color: #fff;
  border: 0.01rem solid #aaa;
}
.orderdetail-text{
  margin-top: 0.1rem;
  background-color: #fff;
  padding-left: 0.3rem;
  color: #1a1a1a;
}
.orderdetail-title{
  line-height: 0.9rem;
  font-size: 0.28rem;
  font-weight: bold;
  color: #1a1a1a;
}
.orderdetail-orderNum{
  line-height: 0.84rem;
  border-bottom: 0.01rem solid #eee;
  color: #1a1a1a;
}
.orderdetail-booking-date{
  line-height: 0.84rem;
  color: #1a1a1a;
}
.orderdetail-btn{
  margin-top: 0.3rem;
  width: 90%;
  height: 0.88rem;
  margin-left: 5%;
  background-color: #16a3e6;
  color: #fff;
  line-height:  0.9rem;
  text-align: center;
  border-radius: 0.1rem;
}

</style>



