<template>
  <div class="preson-main">
    <div class="preson-header">
      <router-link to="/my">
        <img class="icon-black" src="../../assets/images/My/sp_icon_back.png">
      </router-link>
      <p class="header-title">个人信息</p>
    </div>
    <div class="center">
      <div class="set-tx center-item">
        <img class="tx" src="../../assets/images/My/my_icon_tx.png">
        <img class="goto-icon" src="../../assets/images/My/home_icon_more.png">
         <span>编辑头像</span>
      </div>
      <div class="set-name center-item">
        昵称
        <img class="goto-icon" src="../../assets/images/My/home_icon_more.png">
        <span>乐派网001</span>
      </div>
      <div class="set-sex center-item">性别
        <img class="goto-icon" src="../../assets/images/My/home_icon_more.png">
        <span>
          <select>
            <option selected="" value="boy">男</option>
            <option value="girl">女</option>
          </select>
        </span>
      </div>
      <div @click="setDate()" class="set-birthday center-item">生日
        <img class="goto-icon" src="../../assets/images/My/home_icon_more.png">
        <span>{{birthday}}</span>
      </div>
    </div>
    <div class="preson-footer">
      确 定
    </div>
  </div>
</template>

<script>
export default {
  name: 'Person',
  data () {
    return {
      birthday: "1994-10-10",
    }
  },
  methods:{
    setDate: function(){
     this.$picker.show({
       type:'datePicker',
       onOk: (date) =>{
         this.birthday = date
        }
      });
    },
  }
}
</script>

<style>
 .preson-footer{
  position: fixed;
  width: 100%;
  bottom: 0;
  height: 0.9rem;
  z-index: 101;
  background-color: #28a6e6;
  color: #fff;
  font-size: 0.3rem;
  text-align: center;
  line-height: 0.95rem;
}
.preson-header{
  display: flex;
  position: relative;
  justify-content: center;
  /* padding-top: 0.3rem; */
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  border-bottom: 1px solid #ddd; 
}
.icon-black{
  position: absolute;
  top: 0.25rem;
  left: 0.35rem;
  float: left;
  height: 0.4rem;
  width: 0.2rem;
}
.header-title{
  font-size: 0.35rem;
}
.center{
  margin-top: 0.9rem;
  display: flex;
  flex-direction: column;
  margin-left: 0.45rem;
  margin-right: 0.45rem;
}
.center-item{
  height: 0.9rem;
  line-height: 0.9rem;
  border-bottom: 1px solid #ddd; 
}
.set-tx{
  margin-top: 0.36rem;
  margin-bottom: 0.34rem;
  height: 1.44rem;
}
.tx{
  height: 1rem;
  width: 1rem;
}
.goto-icon{
  height: 0.2rem;
  width: 0.1rem;
  float: right;
  margin-top: 0.35rem;
}
.center-item span{
  color: #989ea8;
  font-size: 0.26rem;
  line-height: 0.95rem;
  float: right;
  margin-right: 0.26rem;
}
.set-sex select{
  border: none;
  outline: none;
  width: 100%;
  height: 40px;
  line-height: 40px;
  appearance: none;
  background-color: transparent;
  color: #989ea8;
  -webkit-appearance: none;
  -moz-appearance: none;
  padding-left: 60px;
}
</style>



