<template>
  <div class="setting-main">
    <div class="setting-header">
      <router-link to="/my">
        <img class="icon-black" src="../../assets/images/My/sp_icon_back.png">
      </router-link>
      <p class="header-title">设置</p>
    </div>
    <div class="setting-center">
      <div class=" setting-center-item">
        语言
        <img class="goto-icon" src="../../assets/images/My/home_icon_more.png">
        <span>简体中文</span>
      </div>
      <router-link to="/revise" class=" setting-center-item">密码修改
        <img class="goto-icon" src="../../assets/images/My/home_icon_more.png">
      </router-link>
    </div>
    <router-link to="/login" class="setting-footer">
      退出登录
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'Person',
  data () {
    return {
      birthday: "1994-10-10",
    }
  },
  methods:{
    setDate: function(){
     this.$picker.show({
       type:'datePicker',
       onOk: (date) =>{
         this.birthday = date
        }
      });
    },
  }
}
</script>

<style>
 .setting-footer{
  position: fixed;
  width: 100%;
  bottom: 0;
  height: 0.9rem;
  z-index: 101;
  background-color: #28a6e6;
  color: #fff;
  font-size: 0.3rem;
  text-align: center;
  line-height: 0.95rem;
}
.setting-header{
  display: flex;
  position: relative;
  justify-content: center;
  /* padding-top: 0.3rem; */
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  border-bottom: 1px solid #ddd; 
}
.icon-black{
  position: absolute;
  top: 0.25rem;
  left: 0.35rem;
  float: left;
  height: 0.4rem;
  width: 0.2rem;
}
.header-title{
  font-size: 0.35rem;
}
.setting-center{
  margin-top: 0.9rem;
  display: flex;
  flex-direction: column;
  margin-left: 0.45rem;
  margin-right: 0.45rem;
}
.setting-center-item{
  height: 0.9rem;
  line-height: 0.9rem;
  border-bottom: 1px solid #ddd; 
  color: #000;
}
.goto-icon{
  height: 0.2rem;
  width: 0.1rem;
  float: right;
  margin-top: 0.35rem;
}
.setting-center-item span{
  color: #989ea8;
  font-size: 0.26rem;
  line-height: 0.95rem;
  float: right;
  margin-right: 0.26rem;
}
</style>



