<template>
  <div class="seach-main">
    <div class="seach-header">
      <router-link to="/main">
        <img class="icon-black" src="../../assets/images/My/sp_icon_back.png">
      </router-link>
      <p class="header-title">商品列表</p>
    </div>
    <nav class="seach-top-nav">
      <div class="seach-seach">
        <router-link to="/destination" class="seach-top-city">
          <p>{{this.$store.state.city}}</p><span class="iconfont seach-down-arrow">&#xe674;</span>
        </router-link><!-- 
        <div class="seach-seach-area"> -->
        <input type="text" placeholder="迪士尼乐园" to="/seach" class="seach-seach-input" />
         <!--  <img src="../../assets/images/Main/home_icon_search.png">
        </div> --> 
       <div  class="seach-message">
          <img src="../../assets/images/Main/home_icon_xx_s.png">
        </div>
      </div>
      <ul class="seach-type-list">
        <li v-for="item of seachTypeList"  :class="{seach_type_choose : item.type == seachType }"  class="seach-type-item" @click="changeSeachType(item.type)">{{item.name}}</li>
      </ul>
    </nav>   
    <ul class="seach-spu-list">
        <router-link tag="li" v-for="item of hotProductList" :to="'/product/'+item.id" :key="item.id" class="seach-spu-item">
          <img :src="item.img">
          <div class="seach-spu-info">
            <p class="seach-spu-title">
              {{item.title}}
            </p>
            <div class="seach-spu-mark">
              <span class="red-mark">随订随用</span>
              <span>中文服务</span>
              <span>快速确认</span>
            </div>
            <div class="seach-spu-bot">
              <p>
                <span class="seach-spu-score">{{item.score}}分</span>
                <span class="seach-spu-snum">月销量{{item.num}}份</span>
              </p>
              <p>
                <span class="seach-spu-cur">￥</span>
                <span class="seach-spu-price">{{item.price}}</span>
                <span class="seach-spu-text">起</span>
              </p>
            </div>
          </div>
        </router-link>
      </ul>
  </div>
</template>

<script>
export default {
  name: 'Seach',
  data () {
    return {
      seachType: "all",
      seachTypeList: [
        {name: '美食', type: 'fo'},
        {name: '特色体验', type: 'le'},
        {name: '本地交通', type: 'bt'},
        {name: '精选', type: 'jx'},
        {name: '景点', type: 'lt'},
        {name: '一日游', type: 'ot'},
        {name: '接送机', type: 'tr'},
        {name: '全部', type: 'all'}
      ],
      hotProductList: [
        {id: "050", title: "香港迪士尼乐园门票家庭票+迪士尼套票",num: "2556", score: "4.9", price: "358", img: require("../../assets/images/Main/hot_spu_1.png")},
        {id: "121", title: "香港迪士尼乐园门票家庭票+迪士尼套票",num: "896",  score: "4.8", price: "591", img: require("../../assets/images/Main/hot_spu_2.png")},
        {id: "542", title: "香港迪士尼乐园门票家庭票+迪士尼套票",num: "5555",  score: "4.5", price: "556", img: require("../../assets/images/Main/hot_spu_3.png")},
        {id: "164", title: "香港迪士尼乐园门票家庭票+迪士尼套票",num: "754",  score: "4.0", price: "888", img: require("../../assets/images/Main/hot_spu_4.png")},
         {id: "0150", title: "香港迪士尼乐园门票家庭票+迪士尼套票",num: "2556", score: "4.9", price: "358", img: require("../../assets/images/Main/hot_spu_1.png")},
        {id: "1121", title: "香港迪士尼乐园门票家庭票+迪士尼套票",num: "896",  score: "4.8", price: "591", img: require("../../assets/images/Main/hot_spu_2.png")},
        {id: "5412", title: "香港迪士尼乐园门票家庭票+迪士尼套票",num: "5555",  score: "4.5", price: "556", img: require("../../assets/images/Main/hot_spu_3.png")},
        {id: "1164", title: "香港迪士尼乐园门票家庭票+迪士尼套票",num: "754",  score: "4.0", price: "888", img: require("../../assets/images/Main/hot_spu_4.png")},
      ],
    }
  },
  methods:{
    changeSeachType: function (type) {
      this.seachType = type;
    }
  },

   beforeCreate: function(){
       this.$store.commit('showNav',false);
    },
    created: function(){
        this.seachType = this.$route.params.id;
    },
    beforeMount:function(){
        console.log('挂载到dom前');
    },
    mounted: function(){
        console.log('挂载到dom后');
    },
    beforeUpdate:function(){
        console.log('数据变化更新前');
    },
    updated:function(){
        console.log('数据变化更新后');
    },
    beforeDestroy:function(){
        console.log('vue实例销毁前');
    },
    destroyed:function(){
        console.log('vue实例销毁后');
    }
}
</script>

<style>
.seach-header{
  top: 0;
  position: fixed;
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center; 
  width: 100%;
  background-color: #fff;
}
.icon-black{
  position: absolute;
  top: 0.25rem;
  left: 0.35rem;
  float: left;
  height: 0.4rem;
  width: 0.2rem;
}
.header-title{
  font-size: 0.35rem;
}
/* 顶部搜索 */
.seach-top-nav{
  top: 0.9rem;
  position: fixed;
  width: 100%;
  background-color: #fff;
}
.seach-seach{
  padding: 0.1rem 0;
  display: flex;
  flex-direction: row;

  justify-content: space-around;
}
.seach-seach *{
  display: inline-block;
}
.seach-top-city{
  line-height: 0.60rem;
  font-size: 0.28rem;
  margin-left: 0.2rem;
  color: #000;
  overflow: hidden; 
}
.seach-down-arrow{
  line-height: 0.1rem;
}
.seach-seach-input{
  height: 0.56rem;
  line-height: 0.5rem;
  padding-left: 0.3rem;
  width: 4.5rem;
  background-color: #eeeeee;
  border-radius: 0.28rem;
  font-size: 0.26rem;
  color: #555;
}
/* .seach-seach-area{
  position: relative;
}
.seach-seach-area img{
  position: absolute;
  top: 0.12rem;
  left: 0.25rem;
  z-index: 111;
  height: 0.3rem;
} */
.seach-message{
  line-height: 0.60rem;
  margin-right: 0.1rem;
}
.seach-message img{
  height: 0.4rem;
}
.seach-down-arrow{
  color: #ccc;
}
/* 产品分类选择 */
/* .main-seach{
  margin-top: 0.1rem;
  background-color: #fff;
  padding: 0.5rem 0 0 0.3rem;
} */
.seach-type-list{
  overflow-y: scroll;
  white-space:nowrap;
  display: block;
  margin-left: 0.05rem;
  border-bottom: .01rem solid #ddd;
  border-top: .01rem solid #ddd;
}
.seach-type-item{
  display: inline-block;
  font-size: 0.28rem;
  padding: 0.15rem 0.15rem;
  margin: 0 0.15rem;
}
.seach_type_choose{
  color: #3399cc;
  border-bottom: 0.05rem solid #3399cc;
}
.seach-spu-list{
  margin-top: 2.65rem;
  margin-left: 0.2rem;
}
.seach-spu-item{
  display: flex;
  flex-direction: row;
  margin-top: 0.2rem;
}
.seach-spu-item img{
  width: 1.7rem;
  height: 1.6rem;
}
.seach-spu-info{
  display: flex;
  flex:1;
  height: 1.6rem;
  flex-direction: column;
  justify-content: space-around;
  margin-left: 0.2rem;
  padding-bottom: 0.2rem;
  border-bottom: .01rem solid #ddd;
}
.seach-spu-title{
  font-size: 0.28rem;
  line-height: 0.35rem;
  color: #1a1a1a;
}
.seach-spu-mark {
