}
.seach-spu-mark span{
  display: inline-block;
  font-size: 0.20rem;
  width: 0.9rem;
  height: 0.32rem;
  text-align: center;
  line-height: 0.36rem;
  color: #595959;
  border: 0.01rem solid #e5e5e5;
  border-radius: 0.05rem;
}
.red-mark{
  color: #ff5351 !important;
  border: 0.01rem solid #ff5351 !important;
}
.seach-spu-bot{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.seach-spu-score{
  font-size: 0.22rem;
  color: #3399cc;
}
.seach-spu-snum{
  font-size: 0.22rem;
  color: #8f8f8f;
}
.seach-spu-bot p{
  margin-right: 0.2rem;
}
.seach-spu-cur{
  font-size: 0.20rem;
  color: #ff4242;
}
.seach-spu-price{
  font-size: 0.3rem;
  color: #ff4242;
}
.seach-spu-text{
  font-size: 0.2rem;
  color: #8f8f8f;
}

</style>



