        <p  class="type-icon-title">一日游</p>
      </router-link>
      <router-link tag="li" to="/seach/tr" class="spu-type-icon">
        <img  class="type-icon-img" src="../../assets/images/Main/home_icon_jsj.png">
        <p  class="type-icon-title">接送机</p>
      </router-link>
      <router-link tag="li" to="/seach/all" class="spu-type-icon">
        <img  class="type-icon-img" src="../../assets/images/Main/home_icon_all.png">
