      </div>
      <div class="main-ms-list">
        <router-link v-for="item of msProductList" :key="item.id" tag="div" :to="'/product/'+item.id" class="main-ms-spu">
          <img :src="item.img">
          <p class="ms-spu-title">{{item.title}}</p>
          <p class="ms-spu-price">￥{{item.newPrice}}<span>￥{{item.oldPrice}}</span></p>
