      <h2>热卖榜</h2>
      <ul class="main-hot-area">
        <li @click="changeHotCity(city.id)" v-for="city of hotProductOfCityList" :class="{hot_area_choose: hotProductOfCity == city.id}" class="hot-area-item ">{{city.name}}</li>
      </ul>
      <ul class="hot-spu-list">
        <router-link v-for="item of hotProductList" :key="item.id" tag="li" :to="'/product/'+item.id" class="hot-spu-item">
