  </div>
</template>
<script>
export default {
  name: 'Main',
  data () {
    return {
      scrollOut : false,
      hotProductOfCity: 0,
      productId: "0001",
      msProductList: [
        {id: "1503", title: "【人气爆款】沙巴神山公园一日游", newPrice: "168", oldPrice: "210", img: require("../../assets/images/Main/spu.png")},
        {id: "1502", title: "【人气爆款】沙巴神山公园一日游", newPrice: "168", oldPrice: "210", img: require("../../assets/images/Main/spu.png")},
        {id: "1555", title: "【人气爆款】沙巴神山公园一日游", newPrice: "168", oldPrice: "210", img: require("../../assets/images/Main/spu.png")}
      ],
      choiceProductList: [
        {id: "1575", title: "沙巴人鱼岛一日游",mark: "快速确认", price: "80", img: require("../../assets/images/Main/choice_spu.png")},
        {id: "1452", title: "沙巴人鱼岛一日游",mark: "快速确认",  price: "80", img: require("../../assets/images/Main/choice_spu.png")},
        {id: "1655", title: "沙巴人鱼岛一日游",mark: "快速确认",  price: "80", img: require("../../assets/images/Main/choice_spu.png")},
        {id: "15454", title: "沙巴人鱼岛一日游",mark: "快速确认",  price: "80", img: require("../../assets/images/Main/choice_spu.png")},
      ],
      hotProductOfCityList: [
        {name: "热门", id: "0"},
        {name: "香港", id: "1"},
        {name: "日本", id: "2"},
        {name: "新加坡", id: "3"},
        {name: "马来西亚", id: "4"},
        {name: "泰国", id: "5"},
        {name: "新西兰", id: "6"},
        {name: "韩国", id: "7"},
        {name: "澳大利亚", id: "8"},
      ],
      hotProductList: [
        {id: "050", title: "香港迪士尼乐园门票家庭票+迪士尼套票",num: "2556", price: "358", img: require("../../assets/images/Main/hot_spu_1.png")},
        {id: "121", title: "香港迪士尼乐园门票家庭票+迪士尼套票",num: "896",  price: "591", img: require("../../assets/images/Main/hot_spu_2.png")},
        {id: "542", title: "香港迪士尼乐园门票家庭票+迪士尼套票",num: "5555",  price: "556", img: require("../../assets/images/Main/hot_spu_3.png")},
        {id: "164", title: "香港迪士尼乐园门票家庭票+迪士尼套票",num: "754",  price: "888", img: require("../../assets/images/Main/hot_spu_4.png")},
      ],
      messageIcon: require('../../assets/images/Main/home_icon_xx_n.png'),
      swiperOption: {
        pagination: '.swiper-pagination',
        loop:true
      },
      swiperList: [
        {id:'0001',imgUrl:'https://file.quhappy.com/images/2018/02/05/19/5a02ff4ee5a84f04b7b01bf9581a462b.jpeg'},
        {id:'0002',imgUrl:'https://file.quhappy.com/images/2018/02/02/22/754505a02a7e46bb9bcd0eec6a2da5c1.jpg'}
      ],
      msTime: {day: 0, hours: 0,minutes: 0,seconds: 0}
    }
  },
  methods: {
    handleScroll () {
      if (window.scrollY > 136){
        this.scrollOut = true;
        this.messageIcon = require("../../assets/images/Main/home_icon_xx_s.png");
      }else{
        this.scrollOut = false;
        this.messageIcon = require("../../assets/images/Main/home_icon_xx_n.png");
      }
    },
    changeHotCity: function(id) {
      this.hotProductOfCity = id;
    },
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll);
  },
  beforeCreate: function() {
    this.$store.commit('changeNavIndex',0);
    this.$store.commit('showNav',true);
  },
  created: function() {
    //秒杀倒数计时
    let that = this;
    var interval = setInterval(function(){
      var now = Date.parse(new Date());
      var etime = Date.parse(new Date("2018","9","5","19","00","10"));
      var usedTime = etime - now;  //两个时间戳相差的毫秒数
      that.msTime.day = Math.floor(usedTime/(24*3600*1000));
      var leave1=usedTime%(24*3600*1000)    //计算天数后剩余的毫秒数
      that.msTime.hours=Math.floor(leave1/(3600*1000))
      var leave2=leave1%(3600*1000)        //计算小时数后剩余的毫秒数
      that.msTime.minutes=Math.floor(leave2/(60*1000))
      var leave3=leave2%(60*1000)      //计算分钟数后剩余的毫秒数
      that.msTime.seconds=Math.round(leave3/1000)  
        // clearInterval(interval);
    },1000); 
  }

}
</script>
