  flex: 1;
  width: 100%;
  height: 3.5rem;
  overflow: hidden;
}
.swiper-img{
  width: 100%;
  height: 3.5rem;
}
.warpper >>> .swiper-pagination-bullet-active{
    background: #fff;
}
.main-spu-type{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  background-color: #fff;
  padding-bottom: 0.4rem;
