<template>
  <div class="register-main">
    <div class="register-header">
      <router-link to="/Login">
        <img class="icon-black" src="../../assets/images/My/sp_icon_back.png">
      </router-link>
      <p class="header-title">注册</p>
    </div>
    <div class="register-center">
      <input class="register-input" type="text" name="phoneNum" placeholder="请输入手机号">
      <input    v-model="registerPassword" class="register-input" type="password" name="password" placeholder="请输入密码">
      <div class="register-input register-code-area">
        <input v-model="registerCode" class="register-input-code"  type="text" name="code" placeholder="请输入验证码">
        <button v-bind:class="{connot_get : !getCodeBoolean}" :disabled="!getCodeBoolean" @click="getSmsCode()" class="register-get-code">{{getCode}}</button>
      </div>
      <a v-bind:class="{can_register : canRegister}" :disabled="!canRegister" class="register-btn">注册</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Register',
  data () {
    return {
      canRegister: false,
      registerCode: "",
      registerPassword: "",
      getCodeBoolean: true,
      getCode: "获取验证码",
    }
  },
  methods:{
    inputPasswordeOrCode: function() {
      console.log(this.registerPassword.length);
    },
    getSmsCode: function(){
      var countdown = 60;
      let that = this;
      this.getCodeBoolean = false;
      var interval = setInterval(function(){
        that.getCode = countdown + '秒后可重试';
        console.log(countdown);
        countdown --;
        if(countdown == 0){
          clearInterval(interval);
          that.getCodeBoolean = true;
        }
      },1000); 

    },
    register: function(){
      console.log(1);
    }
  },
  watch: {
    registerPassword: function(val) {
      if(val.length >= 6 && this.registerCode.length == 6){
        this.canRegister = true;
      }else{
        this.canRegister = false;
      }
    },
    registerCode: function(val) {
      if(val.length == 6 && this.registerPassword.length >= 6){
        this.canRegister = true;
      }else if(val.length > 6) {
        this.registerCode = val.substr(0,6);
      }else {
        this.canRegister = false;
      }
    },
    getCodeBoolean: function(val){
      if(val == true){
        this.getCode = "获取验证码";
      }
    }
  }
}

</script>

<style>


.register-main{
  display: flex;
  flex-direction: column;
  padding-bottom: 0.9rem;
}
.register-header{
  position: fixed;
  width: 100%;
  /* margin-top: 0.3rem; */
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  border-bottom: 1px solid #ddd; 
}
.icon-black{
  position: absolute;
  top: 0.25rem;
  left: 0.35rem;
  float: left;
  height: 0.4rem;
  width: 0.2rem;
}
.header-title{
  font-size: 0.35rem;
}
.register-center{
  margin-top: 1rem;
  padding:0 0.8rem;
  display: flex;
  flex-direction: column;

}
.register-input{
  height: 1.1rem;
  line-height: 1.2rem;
  font-size: 0.26rem;
  color: #000;
  border-bottom: 0.01rem solid #eee;
}
.register-get-code{
  height: 0.70rem;
  width: 1.56rem;
  font-size: 0.2rem;
  line-height: 0.8rem;
  text-align: center;
  border-radius: 0.12rem;
  color: #fff;
  margin-top: 0.2rem;
  background-color: #17a2e7;
}
.register-code-area{
   display: flex;
  flex-direction: row;
  justify-content: space-between;
}
input{
  background-color: #f5f5f5;
}
.register-btn{
  text-align: center;
  height: 0.8rem;
  line-height: 0.85rem;
  border-radius: 0.12rem;
  color: #fff;
  margin-top: 0.7rem;
  background-color: #dbdbdb;
}
.can_register{
  background-color: #17a2e7;
}
.connot_get{
  background-color:#ddd;
}
</style>



