import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		showBottomNav: true,
		navIndex: 0,
		token: "",
		phone: "",
		city: "全部"
	},
	// actions: {
	// 	changeCity (ctx,city) {
	// 		ctx.commit('changeCity',city)
	// 	}
	// },
	mutations: {
 		changeCity (state,city) {
 			state.city = city;
		},
		showNav (state,bl) {
			state.showBottomNav = bl;
		},
		changeNavIndex (state,index) {
 			state.navIndex = index;
		}
	}
})


