<template>
  <div class="package-main">
    <div class="package-header">
      <a @click="goBack()">
        <img class="icon-black" src="../../assets/images/My/sp_icon_back.png">
      </a>
      <p class="header-title">选择套餐</p>
    </div>
    <div class="package-center">
      <!-- 套餐类型开始 -->
      <div class="package-type">
        <p class="package-title">套餐类型</p>
        <div class="package-type-list">
          <div v-for="(item,index) of skuList" @click="chooseSku(index)" :class="{package_choose: index == skuIndex}" :key="item.id" class="package-type-item">{{item.skuName}}</div>
        </div>
      </div>
      <!-- 套餐类型结束 -->
      <!-- 套餐介绍开始 -->
      <div class="package-details">
        <p class="package-title">套餐类型</p>
        <div class="package-details-item">
          <div class="package-details-text">{{skuList[skuIndex].skuText}}
          </div>
        </div>
      </div>
      <!-- 套餐介绍结束 -->
      <!-- 出游人群开始 -->
      <div class="package-people">
        <p class="package-title">出游人群</p>
        <div class="package-people-list">
          <div v-for="(item,index) of skuList[skuIndex].ageList" @click="chooseAge(index)" :class="{package_choose: index == ageIndex}" class="package-people-item">{{item.ageName}}</div>
        </div>
      </div>
      <!-- 出游人群结束 -->
      <!-- 出发日期开始 -->
      <div class="package-date">
         <p class="package-title package-date-title">出行日期</p>
         <div class="package-date-choose">
           <ul class="package-date-month-list">
             <li class="package-date-month date-month-choose">9月</li>
             <li class="package-date-month">10月</li>
             <li class="package-date-month">11月</li>
             <li class="package-date-month">12月</li>
           </ul>
           <table class="package-date-tb">
             <thead>
               <tr>
                 <th>日</th>
                 <th>一</th>
                 <th>二</th>
                 <th>三</th>
                 <th>四</th>
                 <th>五</th>
                 <th>六</th>
               </tr>
             </thead>
             <tbody>
              <tr>
                 <td class="date-day-no"></td>
                 <td class="date-day-no"></td>
                 <td class="date-day-no"></td>
                 <td class="date-day-no"></td>
                 <td class="date-day-no">1<p></p></td>
                 <td class="date-day-no">2<p></p></td>
                 <td class="date-day-no">3<p></p></td>
               </tr>
               <tr>
                 <td class="date-day-no">4<p></p></td>
                 <td class="date-day-no">5<p></p></td>
                 <td class="date-day-no">6<p></p></td>
                 <td class="date-day-no">7<p></p></td>
                 <td class="date-day-no">8<p></p></td>
                 <td class="date-day-yes">9<p>￥177</p></td>
                 <td class="date-day-yes">10<p>￥177</p></td>
               </tr>
               <tr>
                 <td class="date-day-yes">11<p>￥177</p></td>
                 <td class="date-day-yes">12<p>￥177</p></td>
                 <td class="date-day-yes">13<p>￥177</p></td>
                 <td class="date-day-yes">14<p>￥177</p></td>
                 <td class="date-day-yes">15<p>￥177</p></td>
                 <td class="date-day-yes">16<p>￥177</p></td>
                 <td class="date-day-yes">17<p>￥177</p></td>
               </tr>
               <tr>
                 <td class="date-day-yes">18<p>￥177</p></td>
                 <td class="date-day-yes">19<p>￥177</p></td>
                 <td class="date-day-yes">20<p>￥177</p></td>
                 <td class="date-day-yes date-day-choose">21<p>￥177</p></td>
                 <td class="date-day-yes">22<p>￥177</p></td>
                 <td class="date-day-yes">23<p>￥177</p></td>
                 <td class="date-day-yes">24<p>￥177</p></td>
               </tr><tr>
                 <td class="date-day-yes">25<p>￥177</p></td>
                 <td class="date-day-yes">26<p>￥177</p></td>
                 <td class="date-day-yes">27<p>￥177</p></td>
                 <td class="date-day-yes">28<p>￥177</p></td>
                 <td class="date-day-yes">29<p>￥177</p></td>
                 <td class="date-day-yes">30<p>￥177</p></td>
                 <td class="date-day-yes"></td>
               </tr>
             </tbody>
           </table>
         </div>
      </div>
      <!-- 出发日期结束 -->
      <!-- 购买数量开始 -->
      <div class="package-buy">
        <div class="package-buy-title">购买数量</div>
        <div class="package-buy-num">
          <button class="package-buy-set " :class="{package_buy_no:number == 0}" @click="delNumber()">-</button>
          <div>{{number}}</div>
          <button class="package-buy-set" @click="addNumber()">+</button>
        </div>
      </div>
      <!-- 购买数量结束 -->
      <!-- 底部按钮 -->
      <div class="package-footer">
        <div class="package-footer-num">
          <span class="package-footer-cur">￥</span>
          <span class="package-footer-price">{{priceSum}}</span>
        </div>
        <router-link to="/booking" class="package-footer-btn">
          立即预定
        </router-link>
      </div>
    </div>
      
  </div>
</template>

<script>
export default {
  name: 'Package',
  data () {
    return {
      skuIndex: 0,
      ageIndex: 0,
      date: 0,
      number: 0,
      skuList : [
        {id: "001", skuName: "1日儿童门票(12岁或以下)", skuText: "墨尔本 莫宁顿半岛薰衣草迷宫+草莓园或观光缆车中文一日游（可选温泉体验）", ageList: [{ageName: "成人", price: 188},{ageName: "儿童", price: 115}]},
        {id: "002", skuName: "1日长者门票(65岁或以上)", skuText: "莫宁顿半岛薰衣草迷宫+莫宁顿半岛薰衣草迷宫+莫宁顿半岛薰衣草迷宫+", ageList: [{ageName: "老人", price: 100},{ageName: "婴儿", price: 50}]},
        {id: "003", skuName: "1日长者门票(65岁或以上)", skuText: "1日长者门票(65岁或以上)1日长者门票(65岁或以上)1日长者门票(65岁或以上)", ageList: [{ageName: "其他", price: 188}]}
      ],
    }
  },
  computed: {
    priceSum: function() {
      return (this.skuList[this.skuIndex].ageList[this.ageIndex].price * this.number);
    }
  },
  methods: {
    goBack: function () { 
      this.$router.go(-1);
    },
    chooseSku: function (index) {
      this.skuIndex = index;
      this.ageIndex = 0;
      this.number = 0;
    },
    chooseAge: function (index) {
      this.ageIndex = index;
      this.number = 0;
    },
    addNumber: function() {
      this.number++;
    },
    delNumber: function() {
      if(this.number > 0){
        this.number--;
      }else{
       
      }
    }
  }
}
</script>

<style>
.package-title{
   font-weight: bold;
  height: 0.8rem;
  line-height: 0.9rem;
  padding-right: 0.3rem;
  border-bottom: 0.01rem  solid #f0f0f0;
  background-color: #fff;
  font-size: 0.28rem;
}

.package-main{
  display: flex;
  flex-direction: column;
}
.package-header{
  position: fixed;
  width: 100%;
  /* margin-top: 0.3rem; */
  background: #fff;
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  /* border-bottom: 1px solid #ddd;  */
}
.icon-black{
  position: absolute;
  top: 0.25rem;
  left: 0.35rem;
  float: left;
  height: 0.4rem;
  width: 0.2rem;
}
.header-title{
  font-size: 0.35rem;
}
.package-center{
  margin-top: 1rem;
  background:  #fff;
}
.package-type{
  padding-left: 0.3rem;
}
.package-type-list{
  display: block;
  margin-bottom: 0.3rem;
}
.package-type-item{
  display: inline-block;
  height: 0.6rem;
  line-height: 0.62rem;
  padding: 0 0.25rem;
  font-size: 0.24rem;
  margin-top: 0.3rem;
  margin-right: 0.2rem;
  color: #1a1a1a;
  background-color: #f5f5f5;
  border-radius: 0.3rem;
}
.package-details{
  padding-left: 0.3rem;
  border-top: 0.1rem solid #f5f5f5; 
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.package-details-item{
  
}
.package-details-text{
  overflow: hidden;
  word-wrap: break-word;
  color: #1a1a1a;
  font-size: 0.22rem;
  line-height: 0.32rem;
  padding: 0.3rem 0;
}

.package-people{
  border-top: 0.1rem solid #f5f5f5; 
  padding-left: 0.3rem;
}
.package-people-list{
  
}
.package-people-item{
  display: inline-block;
  height: 0.6rem;
  width: 1.3rem;
  text-align: center;
  line-height: 0.65rem;
  overflow: hidden;
  font-size: 0.24rem;
  margin: 0.3rem 0;
  margin-right: 0.3rem;
  color: #1a1a1a;
  background-color: #f5f5f5;
  border-radius: 0.3rem;
}
/* 出行时间 */
.package-date{
  border-top: 0.1rem solid #f5f5f5; 
  display: flex;
  flex-direction: column;
}
.package-date-title{
  padding-left: 0.3rem;
}

.package-date-month-list{
  display: flex;
  flex-direction: row;
  border-bottom: 0.01rem  solid #f0f0f0;
}
.package-date-month{
  font-size: 0.3rem;
  color: #585858;
  line-height: 0.88rem;
  padding: 0 0.50rem;

}
.date-month-choose{
   color: #1a1a1a;
   border-bottom: 0.04rem solid #3399cc;
}
.package-date-tb{
  width: 100%;
  text-align: center;
}
.package-date-tb thead{
  border-bottom: 0.01rem  solid #f8f8f8;
  line-height: 0.6rem;
  height: 0.6rem
}
.package-date-tb thead th{
  font-weight: normal;
  color: #585858;
  font-size: 0.26rem;
}
.package-date-tb tbody{
  
}
.package-date-tb tbody tr{
  height: 1.3rem;
}
.package-date-tb tbody td{
  font-size: 0.26rem;
  margin: 0.2rem 0;
}
.date-day-yes{
  color: #4d4d4d;
}
.date-day-no{
  color: #c4c4c4;
}
.package-date-tb tbody td p{
  font-size: 0.2rem;
  height: 0.2rem;
  margin-top: 0.05rem;
}
.date-day-choose{
  border-radius: 0.2rem;
  color: #fff;
  background: linear-gradient(to right, #54c3fc, #24abf0);
}

/* 购买数量 */
.package-buy{
  border-top: 0.1rem solid #f5f5f5; 
  padding: 0.24rem 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  line-height: 0.5rem;
  margin-bottom: 0.9rem;
}
.package-buy-title{
  margin-left: 0.3rem;
  font-size: 0.28rem;
  color: #4d4d4d;
}
.package-buy-num{
  display: flex;
  flex-direction: row;
}
.package-buy-num div{
  font-size: 0.3rem;
  color: #4d4d4d;
}
.package-buy-set{
  height: 0.5rem;
  width: 0.5rem;
  line-height: 0.5rem;
  font-size: 0.4rem;
  margin: 0 0.3rem;
  text-align: center;
  border-radius: 0.1rem;
  background-color: #e5e5e5;
}
.package_buy_no{
  color: #cacaca;
}
/* 底部按钮 */
.package-footer{
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 0.9rem;
  background: #fff;
  z-index: 101;
  display: flex;
  flex-direction: row;
  overflow: hidden;
  justify-content: space-between;
  box-shadow:0 -0.05rem 0.1rem #ebebeb;
}
.package-footer-num{
  width: 70%;
  line-height: 0.9rem;
  color: #ff6631;
  margin-left: 0.3rem;
}
.package-footer-cur{
  font-size: 0.28rem;
}
.package-footer-price{
  
  font-size: 0.6rem;
}
.package-footer-btn{
  width: 30%;
  text-align: center;
  line-height: 0.9rem;
  background-color: #17a1e6;
  color: #fff;
  font-size: 0.3rem;
}

.package_choose{
  color: #fff;
  background: linear-gradient(to right, #54c3fc, #24abf0);
}
</style>



