<template>
  <div class="refund-main">
    <div class="refund-header">
      <a @click="goBack()">
        <img class="icon-black" src="../../assets/images/My/sp_icon_back.png">
      </a>
      <p class="header-title">选择售后类型</p>
    </div>
    <div class="refund-info">
      <div class="refund-item">
        <div class="refund-item-ordernum">订单号：{{item.orderSonNum}}</div>
        <div class="refund-item-info">
          <div class="refund-info-img">
            <img src="../../assets/images/My/order-pic.jpg">
            <!-- <div class="refund-info-img-type">{{item.orderStatus}}</div> -->
          </div>
          <div class="refund-info-text">
            <div class="refund-info-spu">{{item.productTitle}}</div>
            <div class="refund-info-sku">{{item.skuName}}</div>
            <div class="refund-info-bot">
              <div class="refund-info-sendtime">发货时间：{{item.sendTime}}</div>
            </div>
          </div>
        </div>
      </div>
      
      <div v-if="afterSaleType == 1" class="refund-input-item">
        <p>商品状态</p>
        <div>
          <span>
            <select>
              <option value="">请选择</option>
              <option  value="boy">未收到货</option>
              <option value="girl">已收到货</option>
            </select>
          </span>
          <img  src="../../assets/images/My/home_icon_more.png">
        </div>
      </div>
      <div class="refund-input-item">
        <p>套餐</p>
        <div>
          <span>
            <select >
              <option value="">请选择&nbsp;&nbsp;&nbsp;</option>
              <option value="boy">套餐1</option>
              <option value="girl">套餐2</option>
            </select>
          </span>
          <img  src="../../assets/images/My/home_icon_more.png">
        </div>
      </div>
      <div class="refund-input-item">
        <p>数量</p>
        <div>
          <span>
            <select>
              <option value="">请选择&nbsp;&nbsp;&nbsp;</option>
              <option  value="boy">1</option>
              <option value="girl">2</option>
            </select>
          </span>
          <img  src="../../assets/images/My/home_icon_more.png">
        </div>
      </div>
      
      <div class="refund-money-item">
        <div><p>退款金额：</p><span>￥{{refundMoney}}</span></div>
        <p>最多￥{{refundMoney}}，保护发货邮费￥{{postMoney}}</p>
      </div>

      <div class="refund-input-item">
        <p>退款说明：<input type="text" placeholder="选填" name="refundmark"/></p>
      </div>
    </div>
    <router-link to="/order/1" class="refund-footer">
      提交
    </router-link>
  </div>
</template>
<script>
export default {
  name: 'Refund',
  data () {
    return {
      refundMoney: 99.55,
      postMoney: 10.01,
      afterSaleType: 1,
      item: {orderSonNum: "WX150069478702222052", orderSonId: "1156", orderStatus: "已发货", orderStatusId: 1,  productTitle: "香港优异明信片主题咖啡厅【电子票】", skuName: "套餐：咖啡买一送一优惠券", num: 1 ,bookingDate: "2018-10-1 11-41-31", sendTime: "2018-10-7"},
    }
  },
  methods:{
    
    goBack: function () { 
      this.$router.go(-1);
    },
  },
  beforeCreate: function(){
     this.$store.commit('showNav',false);
  },
  created: function(){
      this.afterSaleType = this.$route.params.id;
  },
}
</script>

<style>
.refund-main{
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}
.refund-header{
  position: fixed;
  z-index: 100;
  width: 100%;
  /* margin-top: 0.3rem; */
  background: #fff;
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  /* border-bottom: 0.01rem solid #ddd;  */
}
.icon-black{
  position: absolute;
  top: 0.25rem;
  left: 0.35rem;
  float: left;
  height: 0.4rem;
  width: 0.2rem;
}
.header-title{
  font-size: 0.35rem;
}

.refund-info{
  margin-top: 0.9rem;
}
.refund-item{
  padding-left: 0.28rem;
  background-color: #fff;
  margin: 0.05rem 0;
}
.refund-item-ordernum{
  padding: 0.2rem 0;
  font-size: 0.2rem;
  border-bottom: 0.01rem solid #ddd; 
}
.refund-item-info{
  padding: 0.2rem 0.28rem 0.2rem 0;
  display: flex;
  flex-direction: row; 
}
.refund-info-img{
  height: 1.2rem;
  width: 2rem;
  position: relative;
  border-radius: 0.05rem;
  overflow: hidden;
}
.refund-info-img img{
  width: 100%;
  height: 100%;

}
.refund-info-img-type{
  width: 0.66rem;
  height: 0.36rem;
  position: absolute;
  font-size: 0.18rem;
  line-height: 0.40rem;
  color: #fff;
  text-align: center;
  background-color: #3399cc;
  top: 0;
  left: 0;
}
.refund-info-text{
  display: flex;
  flex-direction: column;
  margin-left: 0.28rem;
  justify-content: space-between;
  color: #1a1a1a;
}
.refund-info-spu{
  font-size: .28rem;
  line-height: 0.35rem;
}
.refund-info-sku{
  line-height: 0.35rem;
  font-size: .24rem;
}
.refund-info-bot{
  display: flex;
  line-height: 0.33rem;
  flex-direction: row;
  justify-content: space-between;
}
.refund-info-sendtime{
  font-size: .24rem;
}
.refund-item-btn{
  width: 1.16rem;
  height: 0.36rem;
  line-height: 0.4rem;
  border-radius: 0.2rem;
  text-align: center;
  font-size: 0.22rem;
 color: #aaa;
  background-color: #fff;
  border: 0.01rem solid #aaa;
}
.refund-text{
  margin-top: 0.1rem;
  background-color: #fff;
  padding-left: 0.3rem;
  color: #1a1a1a;
}
.refund-title{
  line-height: 0.9rem;
  font-size: 0.28rem;
  font-weight: bold;
  color: #1a1a1a;
}
.refund-orderNum{
  line-height: 0.84rem;
  border-bottom: 0.01rem solid #eee;
  color: #1a1a1a;
}
.refund-booking-date{
  line-height: 0.84rem;
  color: #1a1a1a;
}
/* 输入框 */
.goto-icon{
  height: 0.2rem;
  width: 0.1rem;
  float: right;
  margin-top: 0.35rem;
}
.refund-input-item{
  margin-top: 0.1rem;
  height: 0.8rem;
  line-height: 0.85rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #fff;
  padding: 0 0.3rem;
}
.refund-input-item p{
  font-size: 0.26rem;
  color: #1a1a1a;
}
.refund-input-item div{
  display: flex;
  flex-direction: row;

}
.refund-input-item div img{
  margin-top: 0.3rem;
  height: 0.20rem;
}
.refund-input-item div span{
  color: #989ea8;
  font-size: 0.28rem;
  line-height: 0.85rem;
  float: right;
  margin-right: 0.20rem;
}
.refund-input-item select{
  border: none;
  outline: none;
  width: 100%;
  height: 0.4rem;
  line-height: 0.4rem;
  appearance: none;
  background-color: transparent;
  color: #989ea8;
  -webkit-appearance: none;
  -moz-appearance: none;
  padding-left: 60px;
}

.refund-money-item{
  margin-top: 0.1rem;
}
.refund-money-item div{
  padding-left: 0.3rem;
  height: 0.8rem;
  line-height: 0.85rem;
  background-color: #fff;
  font-size: 0.26rem
}
.refund-money-item div p{
  display: inline-block;
}
.refund-money-item div span{
  color: #ff8800;
}
.refund-money-item>p{
  padding-left: 0.3rem;
  color: #7f7f7f;
  font-size: 0.22rem;
  height: 0.6rem;
  line-height: 0.65rem;
}
.refund-footer{
  position: fixed;
  width: 100%;
  bottom: 0;
  height: 0.9rem;
  z-index: 101;
  background-color: #28a6e6;
  color: #fff;
  font-size: 0.3rem;
  text-align: center;
  line-height: 0.95rem;
}
</style>



