<template>
  <div class="login-main">
    <div class="login-header">
      <router-link to="/my">
        <img class="icon-black" src="../../assets/images/My/sp_icon_back.png">
      </router-link>
    </div>
    <div class="login-center-area">
      <p class="login-top">您好，</p>
      <p class="login-title">欢迎来到乐派网商城，立即 <router-link class="login-to-register" to="/register">注册</router-link></p>
      <input class="login-input" type="text" name="phoneNum" placeholder="请输入手机号">
      <div v-if="loginBoolean" class="login-input login-code-area">
        <input    v-model="loginPassword" class="login-input" type="password" name="password" placeholder="请输入密码">
        <router-link to="/retrieve" class="login-forget-password">忘记密码</router-link>
       </div>
      <div v-else class="login-input login-code-area">
        <input v-model="loginCode" class="login-input-code"  type="text" name="code" placeholder="请输入验证码">
        <button v-bind:class="{connot_get : !getCodeBoolean}" :disabled="!getCodeBoolean" @click="getSmsCode()" class="login-get-code">{{getCode}}</button>
      </div>
      <div class="login-btn-area">
        <span @click="changeLoginType()" class="login-type">{{loginType}}</span> 
        <button v-bind:class="{can_click : canClick}" :disabled="!canClick" @click="login()" class="login-btn">登 录</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      loginBoolean: true,
      loginType: "验证码登陆",
      canClick: false,
      loginCode: "",
      loginPassword: "",
      getCodeBoolean: true,
      getCode: "获取验证码",
    }
  },
  methods:{
    changeLoginType: function() {
      this.loginBoolean = !this.loginBoolean;
      this.loginType = this.loginBoolean ? "验证码登录" : "密码登录" ;
      this.loginPassword = "";
      this.loginCode = "";
    },
    inputPasswordeOrCode: function() {
      console.log(this.loginPassword.length);
    },
    getSmsCode: function(){
      var countdown = 60;
      let that = this;
      this.getCodeBoolean = false;
      var interval = setInterval(function(){
        that.getCode = countdown + '秒后可重试';
        console.log(countdown);
        countdown --;
        if(countdown == 0){
          clearInterval(interval);
          that.getCodeBoolean = true;
        }
      },1000); 

    },
    login: function(){
      console.log(1);
    }
  },
  watch: {
    loginPassword: function(val) {
      if(val.length >= 6){
        this.canClick = true;
      }else{
        this.canClick = false;
      }
    },
    loginCode: function(val) {
      if(val.length == 6){
        this.canClick = true;
      }else if(val.length > 6) {
        this.loginCode = val.substr(0,6);
      }else {
        this.canClick = false;
      }
    },
    getCodeBoolean: function(val){
      if(val == true){
        this.getCode = "获取验证码";
      }
    }
  }
}

</script>

<style>
.login-main{
  /* background-color: #fff ; */
}
.login-header{
  position: fixed;
  height: 0.9rem;
  width: 100%;
  line-height: 0.9rem;
  top: 0;
  /* background-color: #fff ; */
}
.icon-black{
  position: absolute;
  top: 0.25rem;
  left: 0.35rem;
  float: left;
  height: 0.4rem;
  width: 0.2rem;
}
.login-center-area{
  margin-top: 0.9rem;
  display: flex;
  flex-direction: column;
  margin-left: 0.6rem;
  margin-right: 0.6rem;
}
.login-top{
  margin-top: 0.7rem;
  font-size: 0.66rem;
  color: #333;

}
.login-title{
  margin-top: 0.3rem;
  font-size: 0.3rem;
  color: #808080;
  margin-bottom: 0.84rem;
}
.login-to-register{
  color: #ff8800;
}
.login-input{
  height: 1.1rem;
  line-height: 1.2rem;
  font-size: 0.32rem;
  color: #000;
  border-bottom: 0.01rem solid #ddd;
  background-color: #f5f5f5;
}
.login-forget-password{
  font-size: 0.26rem;
  color: #aaa;
  line-height: 1.8rem;
}
.login-code-area{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.login-input-code{
  background-color: #f5f5f5;
}
.login-get-code{
  height: 0.70rem;
  width: 1.56rem;
  font-size: 0.2rem;
  line-height: 0.8rem;
  text-align: center;
  border-radius: 0.2rem;
  color: #fff;
  margin-top: 0.2rem;
  background-color: #17a2e7;
}
.login-btn-area{
  margin-top: 0.6rem;
  height: 0.9rem;
  line-height: 0.95rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.login-btn{
  height: 0.9rem;
  width: 1.8rem;
  text-align: center;
  background-color: #ddd;
  color: #fff;
  border-radius: 0.45rem;
  background-color: #ddd;
}
.login-type{
  font-size: 0.26rem;
  color: #808080;
}
.can_click{
  background-color: #17a2e7;
}
.connot_get{
  background-color:#ddd;
}
</style>



