  justify-content: space-between;
  height: 0.8rem;
  line-height: 0.9rem;
  padding-right: 0.3rem;
  border-bottom: 0.01rem  solid #f0f0f0;
  font-size: 0.28rem;
}
.product-feedback-title{
  font-weight: bold;
  color: #1a1a1a;
}
.product-see-feedback{
  color: #fb9d00;
}
.product-feedback-area{

}
.product-feedback-user{
  font-size: 0.25rem;
  color: #808080;
  margin-top: 0.3rem;
  margin-bottom: 0.16rem;
}
.product-feedback-user img{
  height: 0.5rem;
  border-radius: 0.25rem;
  margin-right: 0.16rem;
}
.product-feedback-text{
  font-size: 0.22rem;
  color: #1a1a1a;
  line-height: 0.36rem;
}
.product-feedback-date{
  font-size: 0.24rem;
  margin-top: 0.1rem;
  color: #808080;
  margin-bottom: 0.5rem;
}
.product-info-strengths{
  border-top: 0.1rem  solid #f0f0f0;
  padding-left: 0.3rem; 
  border-bottom: 0.1rem  solid #f0f0f0;
}
.product-strengths-title{
  font-weight: bold;
  height: 0.8rem;
  line-height: 0.9rem;
  padding-right: 0.3rem;
  border-bottom: 0.01rem  solid #f0f0f0;
  font-size: 0.28rem;
}
.product-strengths-item{
  margin-bottom: 0.28rem;
}
.product-strengths-text{
  overflow: hidden;
  display: flex;
  flex-direction: row;
  font-size: 0.22rem;
  color: #1a1a1a;
  margin-top: 0.28rem;
}
.product-strengths-text div{
  height: 0.2rem;
  width: 0.2rem;
}
.product-strengths-text img{
  height: 100%;
  width: 100%;
}
.product-strengths-text p{
  margin-left: 0.05rem;
}
.product-instructions-list{
  overflow-y: scroll;/* 
  white-space:nowrap; */
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-left: 0.05rem;
  border-bottom: .01rem solid #ddd;
  background-color: #fff;
}
.product-instructions-list a{
  color: #1a1a1a;
}
.product-instructions-item{
  display: inline-block;
  font-size: 0.28rem;
  padding: 0.15rem 0.15rem;
}
.product-instructions-choose{
  color: #3399cc;
  border-bottom: 0.05rem solid #3399cc;
}

.product-instructions{
  background-color: #fff;
}
/* 行程安排 */
.product-process{
  padding-left: 0.3rem;
  margin-bottom: 0.3rem;
}
.product-process-list{
  margin-top: 0.1rem;
}
.product-process-item{
  display: flex;
  flex-direction: row;
}
.product-process-time{
  width: 0.86rem;
  height: 0.3rem;
  margin-top: 0.25rem;
  background-color: #3399cc;
  color: #fff;
  border-radius: 0.15rem;
  font-size: 0.24rem;
  text-align: center;
  line-height: 0.35rem;
  overflow: hidden;
  margin-right: 0.14rem;
}
.product-process-right{
  padding-top: 0.30rem;
  border-left: 0.01rem dashed #3399cc;
  margin-bottom: 0.02rem;

}

.product-process-place{
  display: flex;
  flex-direction: row;
  margin-bottom: 0.1rem;
  font-size: 0.26rem;
  color: #3399cc;
}
.product-process-icon{
  display: inline-block;
  height: 0rem;
  border-left: 0.14rem solid #3399cc;
  border-top: 0.1rem solid transparent;
  border-bottom: 0.1rem solid transparent;
  width: 0rem;
  margin-right: 0.12rem;
}
.product-process-text{
  line-height: 0.36rem;
  font-size: 0.24rem;
  margin-left: 0.26rem;
}
/* 预定说明 */
.product-booking{
  border-top: 0.1rem  solid #f0f0f0;
  padding-left: 0.3rem;
  background-color: #fff;
  margin-bottom: 0.4rem;
}
.product-booking-list{
  
}
.product-booking-item{
  
}
.product-booking-title{
  margin-top: 0.3rem;
  font-weight: bold;
  line-height: 0.36rem;
  font-size: 0.26rem;
  color: #1a1a1a;
}
.product-booking-text{
  font-size: 0.24rem;
  line-height: 0.34rem;
  color: #4d4d4d;
}

/* 使用流程 */
.product-use{
  border-top: 0.1rem  solid #f0f0f0;
  padding-left: 0.3rem;
  background-color: #fff;
  margin-bottom: 0.2rem;
}
.product-use-list{
  margin-top: 0.25rem;
}
.product-use-item{
  
}
.product-use-title *{
  display: inline-block;
}
.product-use-title {
  display: flex;
  flex-direction: row;
}
.product-use-title p{
  color: #3399cc;
  font-size: 0.26rem;
  line-height: 0.3rem;
  margin-left: 0.05rem;
}
.product-use-icon{
  display: inline-block;
  height: 0.26rem;
  width: 0.26rem;
  text-align: center;
  font-size: 0.20rem;
  color: #fff;
  line-height: 0.3rem;
  overflow: hidden;
  background-color: #3399cc;
  border-radius: 0.13rem;
}
.product-use-text{
  border-left: 0.01rem solid #3399cc;
  margin-left: 0.13rem;
  padding-left: 0.15rem;
  font-size: 0.26rem;
  line-height: 0.4rem;
  margin-top: 0.05rem;
  padding-top: 0.1rem;
  padding-bottom: 0.1rem;
  margin-bottom: 0.05rem;
}
.last-use-text{
  border-left: 0.01rem solid #fff !important;
}

/* 退改流程 */
.product-change{
  border-top: 0.1rem  solid #f0f0f0;
  padding-left: 0.3rem;
  background-color: #fff;
  
}
.product-change-item{
  margin: 0.25rem 0;
  line-height: 0.36rem;
  font-size: 0.26rem;
  color: #4d4d4d;
  padding-right: 0.3rem;
}
/* 景点详情 */
.product-details-area{
  border-top: 0.1rem  solid #f0f0f0;
  padding-left: 0.3rem;
  background-color: #fff;

}
.product-details-info{
  display: flex;
  flex-direction: row;
  margin: 0.2rem 0;
}
.product-details-left{
  width: 2.1rem;
  height: 1.2rem;
}
.product-details-right{
  display: flex;
  flex-direction: row;
  padding-left: 0.2rem;
  margin: 0.30rem 0;
}
.product-details-t{
   width: 4rem;
}
.product-details-right-title{
  font-size: 0.26rem;
  color: #3399cc;
}
.product-details-right-text{
  font-size: 0.22rem;
  color: #808080; 
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: 0.2rem;
}
.product-details-right-img{
  line-height: 0.6rem;
  margin-left: 0.1rem; 
}
.product-details-right-img img{
  height: 0.3rem;
}

/* 店内推荐 */
.product-recommend-area{
  border-top: 0.1rem  solid #f0f0f0;
  padding-left: 0.3rem;
  padding-bottom: 0.28rem;
}
.product-recommend-list{
  margin-top: 0.3rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-right: 0.3rem;
}
.product-recommend-item{

}
.product-recommend-img{
  height: 1.78rem;
  width: 3.2rem;
}
.product-recommend-title{
  margin-top: 0.16rem;
  font-size: 0.26rem;
  line-height: 0.28rem;
  height: 0.84rem;
  color: #000;

}
.product-recommend-can{
  font-size: 0.22rem;
  color: #8f8f8f;
  margin-bottom: 0.12rem;
}
.product-recommend-num{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.product-recommend-cur{
  font-size: 0.22rem;
  color: #ff8800;
}
.product-recommend-price{
  font-size: 0.3rem;
  color: #ff8800;
}
.product-recommend-qi{
  font-size: 0.2rem;
  color: #8f8f8f;
}
.product-recommend-snum{
  line-height: 0.3rem;
  font-size: 0.2rem;
  color: #8f8f8f;
}

/* 底部按钮 */
.product-footer{
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 101;
  height: 0.9rem;
  background-color: #fff;
  display: flex;
  flex-direction: row;
  box-shadow:0 -0.05rem 0.1rem #ebebeb;
}
.product-collection-btn{
  width: 27%;
  text-align: center;

}
.product-collection-icon{
  display: flex;
  flex-direction: column;
  display: inline-block;
  margin-top: 0.2rem;
  color: #999;
  width: 0.5rem;
  font-size: 0.24rem;
}
.product-collection-icon img{
  height: 0.3rem;
  width: 0.4rem;
  margin-bottom: 0.1rem;
}
.product-footer-btn{
  width: 73%;
  text-align: center;
  line-height: 0.98rem;
  background-color: #16a3e6;
