<template>
  <div class="main">
    <div class="header">
      <div @click="goBack()">
        <img class="icon-black" src="../../assets/images/My/sp_icon_back.png">
      </div>
      <p class="header-title">联系我们</p>
    </div>
    <div class="contact-tel">
      <p>服务电话</p>
      <a href="tel:8613533380042">086 13533380042</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Contact',
  methods:{
    goBack: function () {
      this.$router.go(-1);
    }
  },
  beforeCreate: function(){
     this.$store.commit('showNav',false);
  },
}
</script>

<style>
.main{
  display: flex;
  flex-direction: column;
}
.header{
  position: fixed;
  width: 100%;
  /* margin-top: 0.3rem; */
  background: #fff;
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  border-bottom: 1px solid #ddd; 
}
.icon-black{
  position: absolute;
  top: 0.25rem;
  left: 0.35rem;
  float: left;
  height: 0.4rem;
  width: 0.2rem;
}
.header-title{
  font-size: 0.35rem;
}
.contact-tel{
  margin-top: 1rem;
  background-color: #fff;
  line-height: 0.9rem;
  padding-left: 0.3rem;
}
.contact-tel p{
  border-bottom: 0.01rem solid #ddd;
}
</style>



