<template>
  <div class="Index-main">
    <img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1538141309937&di=ae537e89dd9918081e17ecfb1d2a3b91&imgtype=0&src=http%3A%2F%2Fimg.zcool.cn%2Fcommunity%2F0178fc57a199730000012e7e0b9559.jpg%402o.jpg">
  </div>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
    }
  },
  mounted: function(){
    var that = this;
    setTimeout(function(){
       that.$router.replace({ path: "/main" }) 
    },2000);
  },
   beforeCreate: function() {
    this.$store.commit('showNav',false);
  },
}
</script>

<style>
.Index-main{
  height: 100%;
  width: 100%;

}
.Index-main img{
  height: 100%;
  width: 100%;
}
</style>



