<template>
  <div class="revise-main">
    <div class="revise-header">
      <router-link to="/setting">
        <img class="icon-black" src="../../assets/images/My/sp_icon_back.png">
      </router-link>
      <p class="header-title">修改密码</p>
    </div>
    
    <div  class="revise-next">
      <input    v-model="oldPassword" class="revise-input" type="password" name="oldpassword" placeholder="请输入旧密码">
      <input    v-model="revisePassword" class="revise-input" type="password" name="newpassword" placeholder="请输入新密码">
     <input    v-model="revisetwoPassword" class="revise-input" type="password" name="password" placeholder="请输入确认密码">
      <a v-bind:class="{can_revise : canRevise}" :disabled="!canRevise" @click="revise()" class="revise-btn">确认</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Revise',
  data () {
    return {
      canRevise: false,
      revisePhone: "",
      reviseCode: "",
      oldPassword: "",
      revisePassword: "",
      revisetwoPassword: "",
      getCodeBoolean: true,
      getCode: "获取验证码",
    }
  },
  methods:{
    revise: function(){
      this.$router.push('/login')
    }
  },
  watch: {
    oldPassword: function(val) {
      if(val.length >= 6 && this.revisetwoPassword == this.revisePassword && this.revisetwoPassword.length >= 6){
        this.canRevise = true;
      }else{
        this.canRevise = false;
      }
    },
    revisePassword: function(val) {
      if(val.length >= 6 && this.revisetwoPassword == val && this.oldPassword.length >= 6){
        this.canRevise = true;
      }else{
        this.canRevise = false;
      }
    },
    revisetwoPassword: function(val) {
      if(val.length >= 6 && this.revisePassword == val && this.oldPassword.length >= 6){
        this.canRevise = true;
      }else{
        this.canRevise = false;
      }
    },
  }
}

</script>

<style>


.revise-main{
  display: flex;
  flex-direction: column;
  padding-bottom: 0.9rem;
}
.revise-header{
  position: fixed;
  width: 100%;
  /* margin-top: 0.3rem; */
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  border-bottom: 1px solid #ddd; 
}
.icon-black{
  position: absolute;
  top: 0.25rem;
  left: 0.35rem;
  float: left;
  height: 0.4rem;
  width: 0.2rem;
}
.header-title{
  font-size: 0.35rem;
}
.revise-center{
  margin-top: 1rem;
  padding:0 0.8rem;
  display: flex;
  flex-direction: column;

}
.revise-next{
   margin-top: 1rem;
  padding:0 0.8rem;
  display: flex;
  flex-direction: column;
}
.revise-input{
  height: 1.1rem;
  line-height: 1.2rem;
  font-size: 0.26rem;
  color: #000;
  border-bottom: 0.01rem solid #eee;
}
.revise-get-code{
  height: 0.70rem;
  width: 1.56rem;
  font-size: 0.2rem;
  line-height: 0.75rem;
  text-align: center;
  border-radius: 0.12rem;
  color: #fff;
  margin-top: 0.2rem;
  background-color: #17a2e7;
}
.revise-code-area{
   display: flex;
  flex-direction: row;
  justify-content: space-between;
}
input{
  background-color: #f5f5f5;
}
.revise-btn{
  text-align: center;
  height: 0.8rem;
  line-height: 0.85rem;
  border-radius: 0.12rem;
  color: #fff;
  margin-top: 0.7rem;
  background-color: #dbdbdb;
}
.can_revise{
  background-color: #17a2e7;
}
.connot_get{
  background-color:#ddd;
}
</style>



