<template>
  <div class="order-main">
    <div class="order-header">
      <a @click="goBack()">
        <img class="icon-black" src="../../assets/images/My/sp_icon_back.png">
      </a>
      <p class="header-title">我的订单</p>
    </div>
    <nav class="order-type">
      <ul class="order-type-list">
        <li @click="chooseOrderStatus(item.id)" v-for="item of orderStatusList" :class="{choose_order_type: orderStatus == item.id}" class="order-type-item " :key="item.id">{{item.status}}</li>
      </ul>
    </nav>
    <ul class="order-list">
      <li v-for="(item, index) of orderdList"  class="order-item">
        <div class="order-item-ordernum">订单号：{{item.orderSonNum}}</div>
        <div class="order-item-info">
          <div class="order-info-img">
            <img src="../../assets/images/My/order-pic.jpg">
            <div class="order-info-img-type">{{item.orderStatus}}</div>
          </div>
          <div class="order-info-text">
            <div class="order-info-spu">{{item.productTitle}}</div>
            <div class="order-info-sku">{{item.skuName}}</div>
            <div class="order-info-bot">
              <div class="order-info-num">数量： {{item.num}}张</div>
              <div @click="gotoOrder(index)" :class="{order_item_btn2: item.orderStatusId != 1}" class="order-item-btn">{{orderStatusBtn(item.orderStatusId)}}</div>
            </div>
          </div>
        </div>
      </li>
      
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Order',
  data () {
    return {
      orderStatus: 1,
      orderStatusList : [
        {status: "订单", id: 1},
        {status: "待付款", id: 2},
        {status: "待发货", id: 3},
        {status: "退款售后", id: 4},
      ],
      orderdList: [
        {orderSonNum: "WX150069478702222052", orderSonId: "1156", orderStatus: "待付款", orderStatusId: 1,  productTitle: "香港优异明信片主题咖啡厅【电子票】", skuName: "套餐：咖啡买一送一优惠券", num: 1 },
        {orderSonNum: "WX150069478702222052", orderSonId: "1157", orderStatus: "发货中", orderStatusId: 2,  productTitle: "香港优异明信片主题咖啡厅【电子票】", skuName: "套餐：咖啡买一送一优惠券", num: 1 },
        {orderSonNum: "WX150069478702222052", orderSonId: "1158", orderStatus: "已发货", orderStatusId: 3,  productTitle: "香港优异明信片主题咖啡厅【电子票】", skuName: "套餐：咖啡买一送一优惠券", num: 1 },
      ],
    }
  },
  computed:{
    
  },
  methods:{
    chooseOrderStatus: function(id){
      this.orderStatus = id;
    },
    gotoOrder: function(index) {
      switch(this.orderdList[index].orderStatusId){
        case 1: this.$router.push({path: '/payment'});
          break;
        case 2: this.$router.push({path: '/orderdetail/1'});
          break;
        case 3: this.$router.push({path: '/orderdetail/1'});
         break;
        default: ;
         break;
      }
    },
    orderStatusBtn: function(id) {
      var btn = "";
      switch(id){
        case 1: btn = "立即付款";
          break;
        case 2: btn = "详情";
          break;
        case 3: btn = "详情";
         break;
        default: btn = "";
         break;
      }
      return btn;
    },
    goBack: function () { 
      this.$router.replace('/my');
    },
  },
  beforeCreate: function(){
     this.$store.commit('showNav',false);
  },
  created: function(){
      this.orderStatus = this.$route.params.id;
  },
}
</script>

<style>
.order-main{
  display: flex;
  flex-direction: column;
}
.order-header{
  position: fixed;
  z-index: 100;
  width: 100%;
  /* margin-top: 0.3rem; */
  background: #fff;
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  border-bottom: 0.01rem solid #ddd; 
}
.icon-black{
  position: absolute;
  top: 0.25rem;
  left: 0.35rem;
  float: left;
  height: 0.4rem;
  width: 0.2rem;
}
.header-title{
  font-size: 0.35rem;
}
.order-type{
  width: 100%;
  height: 0.64rem;
  position: fixed;
  z-index: 100;
  top: 0.9rem;
  background-color: #fff;
  border-top: 0.01rem solid #ddd;
  border-bottom: 0.01rem solid #ddd; 
}
.order-type-list{
  line-height: 0.64rem;
  height: 0.64rem;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.order-type-item{
  padding: 0 0.15rem;
  font-size: 0.3rem;
}
.choose_order_type{
  border-bottom: 0.05rem solid #3399cc;
  color: #3399cc;
}
.order-list{
  display: flex;
  flex-direction: column; 
  flex: 1;
  margin-top: 1.7rem;
}
.order-item{
  padding-left: 0.28rem;
  background-color: #fff;
  margin: 0.05rem 0;
}
.order-item-ordernum{
  padding: 0.2rem 0;
  font-size: 0.2rem;
  border-bottom: 0.01rem solid #ddd; 
}
.order-item-info{
  padding: 0.2rem 0.28rem 0.2rem 0;
  display: flex;
  flex-direction: row; 
}
.order-info-img{
  height: 1.2rem;
  width: 2rem;
  position: relative;
  border-radius: 0.05rem;
  overflow: hidden;
}
.order-info-img img{
  width: 100%;
  height: 100%;

}
.order-info-img-type{
  width: 0.66rem;
  height: 0.36rem;
  position: absolute;
  font-size: 0.18rem;
  line-height: 0.40rem;
  color: #fff;
  text-align: center;
  background-color: #3399cc;
  top: 0;
  left: 0;
}
.order-info-text{
  display: flex;
  flex-direction: column;
  margin-left: 0.28rem;
  justify-content: space-between;
  color: #1a1a1a;
}
.order-info-spu{
  font-size: .28rem;
}
.order-info-sku{
  line-height: 0.35rem;
  font-size: .24rem;
}
.order-info-bot{
  display: flex;
  line-height: 0.43rem;
  flex-direction: row;
  justify-content: space-between;
}
.order-info-num{
  font-size: .24rem;
}
.order-item-btn{
  width: 1.16rem;
  height: 0.36rem;
  line-height: 0.4rem;
  border-radius: 0.2rem;
  text-align: center;
  font-size: 0.22rem;
  color: #fff;
  background-color: #FE6E6E;
  border: 0.01rem solid #FE6E6E;
}
.order_item_btn2{
  color: #aaa;
  background-color: #fff;
  border: 0.01rem solid #aaa;
}
</style>



