<template>
  <div id="app">
    <div class="footer" v-if="this.$store.state.showBottomNav">
      <div class="item" @click="handleNavItem(item.id)" v-for="item of navList" :class="{item_choose: item.id == navIndex}" :key="item.id">
        <img :src="item.id == navIndex ? item.clickImg : item.img">
        <p>{{item.name}}</p>
      </div>
    </div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      navList: [
      {name: "首页", path: "/main", img: require("./assets/images/Main/home_icon_home_s.png"), clickImg: require("./assets/images/Main/home_icon_home_n.png"), id: 0},
      {name: "目的地", path: "/destination", img: require("./assets/images/Main/home_icon_mdd_s.png"), clickImg: require("./assets/images/Main/home_icon_mdd_n.png"), id: 1},
      {name: "行程", path: "/scheduling", img: require("./assets/images/Main/home_icon_xc_s.png"), clickImg: require("./assets/images/Main/home_icon_xc_n.png"), id: 2},
      {name: "我的", path: "/my", img: require("./assets/images/Main/home_icon_my_s.png"), clickImg: require("./assets/images/Main/home_icon_my_n.png"), id: 3}
      ],
    }
  },
  methods: {
    handleNavItem: function(index){
      this.$router.replace({ path: this.navList[index].path }) 
    }
  },
  computed: {
    showBottomNav: function () {
      return this.$store.state.showBottomNav;
    },
    navIndex: function () {
      return this.$store.state.navIndex
    }
  }
}
</script>

<style>
#app {
}
.footer{
  position: fixed;
  z-index: 100;
  height: 0.9rem;
  display: flex;
  width: 100%;
  bottom: 0;
  justify-content: space-around;
  border-top: 1px solid #ddd;
  background-color: #fff;
  }
  .item{
    padding-top: 0.13rem;
    text-align: center;
    width: 25%;
    font-size: 0.2rem;
    color: #8c8c8c;
  }
  .item img{
    height: 0.4rem;
  }
  .item p{
    margin-top: 0.1rem;
  }
  .item_choose{
    color: #3399cc;
  }
</style>



