<template>
  <div class="payment-main">
    <div class="payment-header">
      <div @click="goBack()">
        <img class="icon-black" src="../../assets/images/My/sp_icon_back.png">
      </div>
      <p class="header-title">订单支付</p>
    </div>
    <div class="payment-center">
      <p class="payment-title">订单信息</p>
      <div class="payment-booking-info">
        <span class="payment-booking-info-name">订单号：</span>
        <span class="payment-booking-info-text">WX156156416516515641</span>
      </div>
      <div class="payment-booking-info">
        <span class="payment-booking-info-name">商品名：</span>
        <span class="payment-booking-info-text">迪士尼乐园电子票</span>
      </div>
      <div class="payment-booking-info">
        <span class="payment-booking-info-name">套餐名：</span>
        <span class="payment-booking-info-text">一日长者票</span>
      </div>
      <div class="payment-booking-info">
        <span class="payment-booking-info-name">数量：</span>
        <span class="payment-booking-info-text">1</span>
      </div>
      <div class="payment-booking-info">
        <span class="payment-booking-info-name">价格</span>
        <span class="payment-booking-info-text">￥195</span>
      </div>
    </div>
    <router-link to="/my">
    <div class="payment-footer">
      立即支付
    </div>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'Payment',
  data () {
    return {
     
    }
  },
  methods:{
    goBack: function () {
      this.$router.go(-1);
    }
  }
  
}

</script>

<style>
.payment-title{
   font-weight: bold;
  height: 0.8rem;
  line-height: 0.9rem;
  padding-right: 0.3rem;
  border-bottom: 0.01rem  solid #f0f0f0;
  background-color: #fff;
  font-size: 0.28rem;
}
.header-title{
  font-size: 0.35rem;
}
.payment-main{
  display: flex;
  flex-direction: column;
  padding-bottom: 0.9rem;
}
.payment-header{
  position: fixed;
  width: 100%;
  /* margin-top: 0.3rem; */
  background: #fff;
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  /* border-bottom: 1px solid #ddd;  */
}
.icon-black{
  position: absolute;
  top: 0.25rem;
  left: 0.35rem;
  float: left;
  height: 0.4rem;
  width: 0.2rem;
}
.payment-center{
  padding-left:0.3rem; 
  margin-top: 1rem;
  background-color: #fff;
}
.payment-booking-info{
  height: 0.80rem;
  line-height: 0.85rem;
  border-bottom: 0.01rem  solid #f0f0f0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 0.26rem;
}
.payment-booking-info-name{
  color: #1a1a1a;
}
.payment-booking-info-text{
  color: #808080;
  margin-right: 0.3rem;
}

.payment-footer{
  height: 0.9rem;
  font-size: 0.28rem;
  width: 100%;
  z-index: 101;
  background: linear-gradient(to right, #54c3fc, #24abf0);
  color: #fff;
  text-align: center;
  line-height: 0.9rem;
  position: fixed;
  bottom: 0;
}
</style>



