<template>
  <div class="main">
    <div class="header">
      <div @click="goBack()">
        <img class="icon-black" src="../../assets/images/My/sp_icon_back.png">
      </div>
      <p class="header-title">我的保险</p>
    </div>
    <div class="insurance-list">
      <div v-for="item of insuranceList" class="insurance-item">
        <div class="insurance-info-left">
          <h2 class="insurance-info-title">{{item.insuranceTitle}}</h2>
          <p class="insurance-info-num">保险单号：{{item.insuranceNum}}</p>
          <p class="insurance-info-date">保障期限：{{item.insuranceStartTime}}至{{item.insuranceEndTime}}</p>
        </div>
        <div v-if="item.havePDF" class="insurance-info-right">
          <img src="../../assets/images/My/my_icon_bx.png">
          <p>查看凭证></p>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
export default {
  name: 'Insurance',
  data () {
    return {
      insuranceList:[
        {insuranceTitle: "境外畅行无忧", insuranceNum: "56561651112561566641616", insuranceStartTime: "2018-09-10", insuranceEndTime: "2018-09-20", havePDF: true},
        {insuranceTitle: "境外畅行无忧", insuranceNum: "56561651112561566641616", insuranceStartTime: "2018-09-20", insuranceEndTime: "2018-09-30", havePDF: false},
        {insuranceTitle: "境外畅行无忧", insuranceNum: "56561651112561566641616", insuranceStartTime: "2018-09-30", insuranceEndTime: "2018-10-20", havePDF: true},
      ],
    }
  },
  methods:{
    goBack: function () {
      this.$router.go(-1);
    }
  },
  beforeCreate: function(){
     this.$store.commit('showNav',false);
  },
}
</script>

<style>
.main{
  display: flex;
  flex-direction: column;
}
.header{
  position: fixed;
  width: 100%;
  /* margin-top: 0.3rem; */
  background: #fff;
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  border-bottom: 1px solid #ddd; 
}
.icon-black{
  position: absolute;
  top: 0.25rem;
  left: 0.35rem;
  float: left;
  height: 0.4rem;
  width: 0.2rem;
}
.header-title{
  font-size: 0.35rem;
}
.insurance-list{
  margin-top: 1.2rem;
}
.insurance-item{
  height: 2rem;
  background-color: #fff;
  margin: 0 .3rem;
  margin-bottom: 0.3rem;
  box-shadow: 0 0 0.2rem #e5e5e5;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

}
.insurance-info-left{
  padding-left: 0.3rem;
}
.insurance-info-title{
  font-size: 0.32rem;
  font-weight: bold;
  margin-top: 0.3rem;
}
.insurance-info-num{
  margin-top: 0.24rem;
  font-size: 0.24rem;
  color: #1a1a1a;
}
.insurance-info-date{
  margin-top: 0.4rem;
  font-size: 0.24rem;
  color: #959595;
}
.insurance-info-right{
  background-color: #18a2e5;
  width: 1.6rem;
  font-size: 0.24rem;text-align: center;
  color: #fff;
  line-height: 2rem;
  position: relative;
}
.insurance-info-right p{
  z-index: 2;
}
.insurance-info-right img{
  position: absolute;
  z-index: 1;
  width: 1.1rem;
  height: 1.2rem;
  left: 0.25rem;
  top: 0.4rem;
}
</style>



