<template>
  <div class="main">
    <div class="header">
      <div @click="goBack()">
        <img class="icon-black" src="../../assets/images/My/sp_icon_back.png">
      </div>
      <p class="header-title">优惠券</p>
    </div>
    <div class="coupon-list">
      <div class="coupon-item">
        <i class="coupon-left">&nbsp;</i>
        <div class="coupon-text">
          <p>香港正宗避风塘桥底辣蟹</p>
          <span>八折优惠</span>
        </div>
        <div class="coupon-btn">
          <a >免费领</a>
        </div>
      </div>
      <div class="coupon-item">
        <i class="coupon-left">&nbsp;</i>
        <div class="coupon-text">
          <p>香港正宗避风塘桥底辣蟹</p>
          <span>八折优惠</span>
        </div>
        <div class="coupon-btn">
          <a >免费领</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Coupon',
  methods:{
    goBack: function () {
      this.$router.go(-1);
    }
  }
}
</script>

<style>
.main{
  display: flex;
  flex-direction: column;
}
.header{
  position: fixed;
  width: 100%;
  /* margin-top: 0.3rem; */
  background: #fff;
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  border-bottom: 1px solid #ddd; 
}
.icon-black{
  position: absolute;
  top: 0.25rem;
  left: 0.35rem;
  float: left;
  height: 0.4rem;
  width: 0.2rem;
}
.header-title{
  font-size: 0.35rem;
}
.coupon-list{
  margin-top: 0.9rem;
  padding: 0.6rem; 
}

.coupon-item{
  margin-bottom: 0.2rem;
  background-color: #fff2f2;
  position: relative;
  display: flex;
  height: 1.18rem;
  flex-direction: row;
  justify-content:  space-between;
  border-radius: 0.06rem;
}
.coupon-left{
  height: 100%;
  width: 0.06rem;
  position: absolute;
  border-radius: 0.6rem 0 0 0.5rem;
  background: linear-gradient(to bottom, #b87bfd, #fe4465);
}
.coupon-text{
  margin: 0.26rem;
}
.coupon-text p{
  font-size: 0.28rem;
  color: #808080;
  margin-bottom: 0.12rem;
}
.coupon-text span{
  font-size: 0.24rem;
  color: #ff7f7f;
}
.coupon-btn{
  margin: 0.15rem 0;
  padding: 0.3rem;
  border-left: 0.01rem dashed #999;
}
.coupon-btn a{
  height: 0.3rem;
  line-height: 0.3rem;
  width: 1rem;
  display: inline-block;
  text-align: center;
  background: linear-gradient(to right, #ff5057, #ff2863);
  border-radius: 0.15rem;
  font-size: 0.20rem;
  color: #fff;
}
.coupon-btn a{
  height: 0.3rem;
  line-height: 0.34rem;
  width: 1rem;
  display: inline-block;
  text-align: center;
  background: linear-gradient(to right, #ff5057, #ff2863);
  border-radius: 0.15rem;
  font-size: 0.20rem;
  color: #fff;
}
</style>



