<template>
  <div class="retrieve-main">
    <div class="retrieve-header">
      <router-link to="/Login">
        <img class="icon-black" src="../../assets/images/My/sp_icon_back.png">
      </router-link>
      <p class="header-title">找回密码</p>
    </div>
    <div v-if="!showNext" class="retrieve-center">
      <input v-model="retrievePhone" class="retrieve-input" type="text" name="phoneNum" placeholder="请输入手机号">
      <!-- <input    v-model="retrievePassword" class="retrieve-input" type="password" name="password" placeholder="请输入密码"> -->
      <div class="retrieve-input retrieve-code-area">
        <input v-model="retrieveCode" class="retrieve-input-code"  type="text" name="code" placeholder="请输入验证码">
        <button v-bind:class="{connot_get : !getCodeBoolean}" :disabled="!getCodeBoolean" @click="getSmsCode()" class="retrieve-get-code">{{getCode}}</button>
      </div>
      <a v-bind:class="{can_retrieve : canNext}" :disabled="!canNext" @click="next()" class="retrieve-btn">下一步</a>
    </div>
    <div v-else class="retrieve-next">
      <input    v-model="retrievePassword" class="retrieve-input" type="password" name="password" placeholder="请输入密码">
     <input    v-model="retrievetwoPassword" class="retrieve-input" type="password" name="password" placeholder="请输入确认密码">
      <a v-bind:class="{can_retrieve : canRetrieve}" :disabled="!canRetrieve" @click="retrieve()" class="retrieve-btn">确认</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Retrieve',
  data () {
    return {
      showNext: false,
      canRetrieve: false,
      canNext: false,
      retrievePhone: "",
      retrieveCode: "",
      retrievePassword: "",
      retrievetwoPassword: "",
      getCodeBoolean: true,
      getCode: "获取验证码",
    }
  },
  methods:{
    inputPasswordeOrCode: function() {
      console.log(this.retrievePassword.length);
    },
    getSmsCode: function(){
      var countdown = 60;
      let that = this;
      this.getCodeBoolean = false;
      var interval = setInterval(function(){
        that.getCode = countdown + '秒后可重试';
        console.log(countdown);
        countdown --;
        if(countdown == 0){
          clearInterval(interval);
          that.getCodeBoolean = true;
        }
      },1000); 
    },
    retrieve: function(){
      this.$router.push('/login')
    },
    next: function(){
      this.showNext = true;
    }
  },
  watch: {
    retrievePassword: function(val) {
      if(val.length >= 6 && this.retrievetwoPassword == val){
        this.canRetrieve = true;
      }else{
        this.canRetrieve = false;
      }
    },
    retrievetwoPassword: function(val) {
      if(val.length >= 6 && this.retrievePassword == val){
        this.canRetrieve = true;
      }else{
        this.canRetrieve = false;
      }
    },
    retrievePhone: function(val) {
      if(this.retrieveCode.length == 6 && val != ""){
        this.canNext = true;
      }else {
        this.canNext = false;
      }
    },
    retrieveCode: function(val) {
      if(val.length == 6 && this.retrievePhone != ""){
        this.canNext = true;
      }else if(val.length > 6) {
        this.retrieveCode = val.substr(0,6);
      }else {
        this.canNext = false;
      }
    },
    getCodeBoolean: function(val){
      if(val == true){
        this.getCode = "获取验证码";
      }
    }
  }
}

</script>

<style>


.retrieve-main{
  display: flex;
  flex-direction: column;
  padding-bottom: 0.9rem;
}
.retrieve-header{
  position: fixed;
  width: 100%;
  /* margin-top: 0.3rem; */
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  border-bottom: 1px solid #ddd; 
}
.icon-black{
  position: absolute;
  top: 0.25rem;
  left: 0.35rem;
  float: left;
  height: 0.4rem;
  width: 0.2rem;
}
.header-title{
  font-size: 0.35rem;
}
.retrieve-center{
  margin-top: 1rem;
  padding:0 0.8rem;
  display: flex;
  flex-direction: column;

}
.retrieve-next{
   margin-top: 1rem;
  padding:0 0.8rem;
  display: flex;
  flex-direction: column;
}
.retrieve-input{
  height: 1.1rem;
  line-height: 1.2rem;
  font-size: 0.26rem;
  color: #000;
  border-bottom: 0.01rem solid #eee;
}
.retrieve-get-code{
  height: 0.70rem;
  width: 1.56rem;
  font-size: 0.2rem;
  line-height: 0.75rem;
  text-align: center;
  border-radius: 0.12rem;
  color: #fff;
  margin-top: 0.2rem;
  background-color: #17a2e7;
}
.retrieve-code-area{
   display: flex;
  flex-direction: row;
  justify-content: space-between;
}
input{
  background-color: #f5f5f5;
}
.retrieve-btn{
  text-align: center;
  height: 0.8rem;
  line-height: 0.85rem;
  border-radius: 0.12rem;
  color: #fff;
  margin-top: 0.7rem;
  background-color: #dbdbdb;
}
.can_retrieve{
  background-color: #17a2e7;
}
.connot_get{
  background-color:#ddd;
}
</style>



