import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Index from '@/pages/Index/Index'
import Main from '@/pages/Main/Main'
import My from '@/pages/My/My'
import Scheduling from '@/pages/Scheduling/Scheduling'
import Destination from '@/pages/Destination/Destination'
import Person from '@/pages/Person/Person'
import Coupon from '@/pages/Coupon/Coupon'
import Order from '@/pages/Order/Order'
import Seach from '@/pages/Seach/Seach'
import Login from '@/pages/Login/Login'
import Register from '@/pages/Register/Register'
import Booking from '@/pages/Booking/Booking'
import Product from '@/pages/Product/Product'
import Package from '@/pages/Package/Package'
import Payment from '@/pages/Payment/Payment'
import Retrieve from '@/pages/Retrieve/Retrieve'
import Setting from '@/pages/Setting/Setting'
import Revise from '@/pages/Revise/Revise'
import Seckill from '@/pages/Seckill/Seckill'
import OrderDetail from '@/pages/OrderDetail/OrderDetail'
import Feedback from '@/pages/Feedback/Feedback'
import Aftersale from '@/pages/Aftersale/Aftersale'
import Refund from '@/pages/Refund/Refund'
import Contact from '@/pages/Contact/Contact'
import Insurance from '@/pages/Insurance/Insurance'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/main',
      name: 'Main',
      component: Main
    },
    {
      path: '/my',
      name: 'My',
      component: My
    },
    {
      path: '/scheduling',
      name: 'Scheduling',
      component: Scheduling
    },
    {
      path: '/destination',
      name: 'Destination',
      component: Destination
    },
    {
      path: '/person',
      name: 'Person',
      component: Person
    },
    {
      path: '/coupon',
      name: 'Coupon',
      component: Coupon
    },
    {
      path: '/order/:id',
      name: 'Order',
      component: Order
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/seach/:id',
      name: 'Seach',
      component: Seach
    },
    {
      path: '/booking',
      name: 'Booking',
      component: Booking
    },
    {
      path: '/product/:id',
      name: 'Product',
      component: Product
    },
    {
      path: '/package',
      name: 'Package',
      component: Package
    },
    {
      path: '/payment',
      name: 'Payment',
      component: Payment
    },
    {
      path: '/retrieve',
      name: 'Retrieve',
      component: Retrieve
    },
    {
      path: '/setting',
      name: 'Setting',
      component: Setting
    },
    {
      path: '/revise',
      name: 'Revise',
      component: Revise
    },
    {
      path: '/seckill',
      name: 'Seckill',
      component: Seckill
    },
    {
      path: '/orderdetail/:id',
      name: 'OrderDetail',
      component: OrderDetail
    },
    {
      path: '/feedback/:id',
      name: 'Feedback',
      component: Feedback
    },
    {
      path: '/Aftersale/:id',
      name: 'Aftersale',
      component: Aftersale
    },
    {
      path: '/refund/:id',
      name: 'Refund',
      component: Refund
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact
    },
    {
      path: '/insurance',
      name: 'Insurance',
      component: Insurance
    },
  ]
})



