      <div class="aftersale-btn-left">
        <img src="../../assets/images/My/sp_icon_thtk.png">
        <p>退货退款</p>
      </div>
      <div class="aftersale-btn-right">
        <p>已收货，需要退回该产品</p>
        <img src="../../assets/images/My/home_icon_more.png">
      </div>
    </router-link>
  </div>
</template>
<script>
export default {
